import { createSlice } from '@reduxjs/toolkit';
import Axios from "axios";
import {v1 as uuid} from "uuid";

/* Just a test code... remove it afterwards */ 
const URI = "https://reflect-backend-2.azurewebsites.net";//process.env.DEV_MONGO_URL;
/* Just a test code... remove it afterwards */
const token = localStorage.getItem('wstoken');
const headers = {
    headers: {
        "auth-token": token
    }
}

const initialState = {
    trainingDatas: []
};

export const botSlice = createSlice({
    name: 'bot',
    initialState,
    reducers: {
        setTrainingData: (state,action) => {
            state.trainingDatas = action.payload
        },
        deleteTrainingData: (state,action) => {
            console.log("Ito yun")
            console.log(action.payload)
            state.trainingDatas = [...state.trainingDatas].filter(
                e=>e._id !== action.payload
            )
            
            
        }
    }
})

export const {
    setTrainingData,
    deleteTrainingData
} = botSlice.actions;

export const actionGetTrainingData = () => (dispatch,getState) => {
    Axios.get(
        `${URI}/bot/all-training-data`,
        headers
    ).then(res=>{
        dispatch(setTrainingData(res.data))
    })
}

export const actionCreateTrainingData = trainingData => (dispatch,getState) => {
    Axios.post(
        `${URI}/bot/create-training-data`,
        trainingData,
        headers
    ).then(res=>{
        console.log(res)
    })
}

export const actionUpdateTrainingData = trainingData => (dispatch,getState) => {
    console.log("nasa actionUpdateTrainingData")
    Axios.put(
        `${URI}/bot/update-training-data`,
        trainingData,
        headers
    ).then(res=>{
        console.log("may response sa actionUpdateTrainingData")
        console.log(res)
    })
}

export const actionDeleteTrainingData = trainingDataId => (dispatch,getState) => {
    Axios.delete(
        `${URI}/bot/${trainingDataId}`,
        headers
    ).then(res=>{
        dispatch(deleteTrainingData(trainingDataId))
    })
}

export default botSlice.reducer;