import React, {useState,useEffect,Fragment,useRef,memo} from 'react';
import './Login.scss';
import * as yup from "yup";
import TextField from '@mui/material/TextField';

import Button from '@mui/material/Button';
import {
    actionAuthenticateToken,
    actionLogin,
    actionShowAccountRegistration
} from '../../app/Reducers/user/userSlice';

import { useForm, Controller } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { useSelector, useDispatch } from 'react-redux';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Axios from "axios";


const schema = yup.object().shape({
    email: yup.string().required("Provide valid email"),
    password: yup.string()
                .required('No password provided.') 
                .min(8, 'Password is too short - should be 8 chars minimum.')
});

const Login = ({
    setView
}) => {

    const dispatch = useDispatch()
    
    const { showAccountRegistration } = useSelector(state => state.user);

    // form setup
    const { control, handleSubmit, reset, formState: {errors,isSubmitSuccessful} } = useForm({ 
        defaultValues: {
            // email: thirdPartyUserData ? thirdPartyUserData.loginHint : "",
            email: "",
            password: ""
        },
        resolver: yupResolver(schema)
    });

    const onSubmit = registrationThirdPartyUserData => {
        const {email, password} = registrationThirdPartyUserData;
        dispatch(actionLogin({email, password}))
    }

    return <div className='LoginContainer form-wrapper'>
        <form className="login-form form-container" onSubmit={handleSubmit(onSubmit)}>
            <h1 className="title">Login</h1>
            <Stack
            direction="column"
            alignItems="center"
            spacing={2}>
                <Controller
                    name="email"
                    control={control}
                    defaultValue={""}
                    render={({field}) => <TextField
                        {...field}
                        className="textArea"
                        label="Email"
                        variant="outlined"
                        placeholder={"email"}
                        error={!!errors.email}
                        type="email"
                        sx={{ width: '100%' }}
                        helperText={errors?.email ? errors?.email?.message : ''}
                    />
                }/>
                <Controller
                    name="password"
                    control={control}
                    defaultValue={""}
                    render={({field}) => <TextField
                        {...field}
                        className="textArea"
                        label="Password"
                        variant="outlined"
                        placeholder={"password"}
                        error={!!errors.password}
                        type="password"
                        sx={{ width: '100%' }}
                        helperText={errors?.password ? errors?.password?.message : ''}
                    />
                }/>
                <Stack
                spacing={1}
                direction="row"
                justifyContent="flex-end">
                    <Button type="submit" variant="outlined">Login</Button>
                    <Button onClick={()=>{setView("registration")}} type="button" variant="outlined">Register</Button>
                </Stack>
            </Stack>

        </form>

        <Button
        onClick={async () => {
            dispatch(actionAuthenticateToken())
            // const URI = "https://reflect-backend-2.azurewebsites.net";//process.env.DEV_MONGO_URL;
            // const res = await Axios.get(`${URI}/users/authenticate-token`,{
            //     headers: {
            //         "auth-token": localStorage.getItem('wstoken')
            //     }
            // })
            // console.log(res)
        }}
        type="button"
        variant="outlined">
            Test query
        </Button>
    </div>
}

export default Login;